"use client";

import { useEffect, useState } from "react";
import {
  FaHome,
  FaHistory,
  FaCalendarAlt,
  FaUser,
  FaHeartbeat,
  FaTint,
  FaCog,
  FaBell,
} from "react-icons/fa";
import { useRouter } from "next/navigation";
import HeartRateChart from "../components/dashboard/HeartRateChart";

export default function Dashboard() {
  const router = useRouter();
  const [currentBpm, setCurrentBpm] = useState(72);
  const [bpmData, setBpmData] = useState<{ time: string; bpm: number }[]>([]);
  const [userName] = useState("John Doe");

  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentBpm((prev) => {
        const newBpm =
          prev +
          (Math.random() > 0.5 ? 1 : -1) * Math.floor(Math.random() * 5);
        return Math.max(60, Math.min(115, newBpm));
      });
      setBpmData((prev) => [
        ...prev.slice(-9),
        { time: new Date().toLocaleTimeString().slice(0, 5), bpm: currentBpm },
      ]);
    }, 3000);
    return () => clearInterval(interval);
  }, [currentBpm]);

  // Fake Min/Avg/Max for stats card
  const minBpm = 65;
  const avgBpm = 80;
  const maxBpm = 115;

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-500 via-purple-500 to-indigo-700 flex flex-col px-4 py-4">
      {/* Header */}
      <div className="rounded-2xl p-4 flex justify-between items-center shadow-lg text-white bg-gradient-to-r from-blue-500 to-purple-600">
        {/* App Name */}
        <div>
          <h1 className="text-xl font-bold">BeatAware</h1>
          <p className="text-xs italic opacity-90">
            ‚ÄúListen to your heart, BeatAware helps you care.‚Äù üíô
          </p>
        </div>

        {/* User Card */}
        <div className="flex items-center gap-3 bg-gradient-to-r from-white to-gray-100 text-gray-800 px-4 py-2 rounded-2xl shadow-lg border">
          <div className="w-9 h-9 flex items-center justify-center bg-gradient-to-r from-blue-400 to-purple-500 text-white font-bold rounded-full shadow">
            {userName.charAt(0)}
          </div>
          <div className="flex flex-col leading-tight">
            <span className="font-semibold">{userName}</span>
            <span className="text-xs text-gray-500">Active User</span>
          </div>
          <div className="flex items-center gap-2 ml-2">
            <button className="w-7 h-7 flex items-center justify-center rounded-full bg-gray-200 hover:bg-blue-100 text-blue-600 transition">
              <FaBell size={12} />
            </button>
            <button className="w-7 h-7 flex items-center justify-center rounded-full bg-gray-200 hover:bg-purple-100 text-purple-600 transition">
              <FaCog size={12} />
            </button>
          </div>
        </div>
      </div>

      {/* Tabs (Vitals Selection) */}
      <div className="flex justify-around mt-5 bg-white rounded-xl shadow-md p-2">
        <button className="flex flex-col items-center text-blue-600 font-semibold">
          <FaHeartbeat size={18} /> <span className="text-xs mt-1">Heart</span>
        </button>
        <button className="flex flex-col items-center text-gray-500 hover:text-blue-600 transition">
          <FaTint size={18} /> <span className="text-xs mt-1">BP</span>
        </button>
        <button className="flex flex-col items-center text-gray-500 hover:text-blue-600 transition">
          <FaUser size={18} /> <span className="text-xs mt-1">Profile</span>
        </button>
      </div>

      {/* Stats Box */}
      <div className="bg-white rounded-2xl p-5 shadow-lg mt-6 text-center">
        <h3 className="text-lg font-semibold mb-4 text-gray-700">Heart Rate</h3>
        <div className="flex justify-around">
          <div>
            <p className="text-green-600 font-bold text-xl">{minBpm}</p>
            <p className="text-sm text-gray-500">Min bpm</p>
          </div>
          <div>
            <p className="text-blue-600 font-bold text-xl">{avgBpm}</p>
            <p className="text-sm text-gray-500">Avg bpm</p>
          </div>
          <div>
            <p className="text-red-500 font-bold text-xl">{maxBpm}</p>
            <p className="text-sm text-gray-500">Max bpm</p>
          </div>
        </div>
      </div>

      {/* Chart */}
      <div className="bg-white rounded-2xl p-5 shadow-lg mt-6">
        <h3 className="text-lg font-semibold mb-3">Recent Heart Rate Trend</h3>
        <HeartRateChart data={bpmData} />
      </div>

      {/* Bottom Navigation */}
      <div className="mt-auto bg-gradient-to-r from-blue-400 to-purple-500 shadow-inner p-2 flex justify-around items-center rounded-t-2xl">
        <button onClick={() => router.push("/dashboard")} className="p-2 text-white">
          <FaHome size={22} />
        </button>
        <button onClick={() => router.push("/dashboard/history")} className="p-2 text-white">
          <FaHistory size={22} />
        </button>
        <button onClick={() => router.push("/dashboard/appointments")} className="p-2 text-white">
          <FaCalendarAlt size={22} />
        </button>
        <button onClick={() => router.push("/dashboard/profile")} className="p-2 text-white">
          <FaUser size={22} />
        </button>
      </div>
    </div>
  );
}
